import { Component, OnInit } from '@angular/core';
import { ApiService } from 'src/app/services/api.service';
import { CommonService } from 'src/app/services/common.service';
import { ModalController } from '@ionic/angular';
import { LoadingController } from '@ionic/angular';
import { ViewQueryPage } from '../view-query/view-query.page';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
@Component({
  selector: 'app-queries',
  templateUrl: './queries.page.html',
  styleUrls: ['./queries.page.scss'],
})
export class QueriesPage implements OnInit {
  queries = [];
  loader;
  role;
  user;
  menuItems = [
    {
      icon: 'home',
      name: 'Home',
      routerLink: './home',
    },
  ];
  constructor(
    private api: ApiService,
    private common: CommonService,
    public modalController: ModalController,
    public location: Location,
    public loadingController: LoadingController,
    private router: Router
  ) {}

  ngOnInit() {
    this.role = JSON.parse(localStorage.getItem('role'));
    this.user = this.common.getUser();
    this.transformMenu();
  }

  ionViewDidEnter() {
    this.role = JSON.parse(localStorage.getItem('role'));
    this.user = this.common.getUser();
    if (this.menuItems.length <= 1) {
      this.role = JSON.parse(localStorage.getItem('role'));
      this.user = this.common.getUser();
      this.transformMenu();
    }
  }

  ionViewWillLeave() {
    this.menuItems = [
      {
        icon: 'home',
        name: 'Home',
        routerLink: './home',
      },
    ];
    console.log('reset nav');
  }

  logout() {
    localStorage.removeItem('user');
    this.router.navigate(['./home']);
    window.location.reload();
  }
  transformMenu() {
    if (
      JSON.parse(localStorage.getItem('user')) === undefined ||
      JSON.parse(localStorage.getItem('user')) === null
    ) {
      console.log('Guest');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'chat',
          name: 'Contact',
          routerLink: './contact',
        }
      );
    } else if (
      this.user !== null &&
      this.user !== undefined &&
      (this.user.role.id === 'a' || this.user.role.id === 'l')
    ) {
      console.log('admin and landlord con');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'account_circle',
          name: 'Profile',
          routerLink: './profile',
        },
        {
          icon: 'people',
          name: 'Tenants',
          routerLink: './tenants',
        },
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: '',
        }
      );
    } else if (
      this.user !== null &&
      this.user !== undefined &&
      this.user.role.id === 't'
    ) {
      console.log('tenant con');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'chat',
          name: 'Contact',
          routerLink: './contact',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: './queries',
        }
      );
    } else {
      console.log('team');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'account_circle',
          name: 'Profile',
          routerLink: './profile',
        },
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: '',
        }
      );
    }
  }
  checkRoute(route) {
    if (route.name === 'Logout') {
      localStorage.clear();
      this.router.navigate(['./home']);
    } else {
      this.router.navigate([route.routerLink]);
    }
  }

  getQueries() {
    const query = {
      'owner.phone': this.common.getUser().phone,
    };
    this.presentLoading();
    this.api.getTenantQueries('query/getQueries', query).subscribe((res) => {
      const newArr = [];
      res.forEach(item => {
        if(item.resolved === false){
          item.unresolved = item.resolved;
          newArr.push(item);
        }
        if(item.resolved){
          newArr.push(item);
        }
      });
      newArr.sort((first, last) => first.unresolved - last.resolved);
      this.queries = newArr;
      this.loader.dismiss();
    });
  }

  async viewQuery(query) {
    await this.modalController
      .create({
        component: ViewQueryPage,
        componentProps: { query },
      })
      .then((res) => res.present());
  }

  async presentLoading() {
    console.log('loader');

    await this.loadingController
      .create({
        cssClass: 'my-custom-class',
        message: 'Please wait...',
      })
      .then((res) => {
        this.loader = res;
        res.present();
      });
  }
}
